<template>
   <div v-if="show">
      <Row>
        <Col>
            <img src="../assets/one.png" width="200px">
            <Divider>الملف الشخصي</Divider>
        </Col>
      </Row>

      <Row type="flex" justify="end">
          
         <Col span="12">
         <span style="padding:10px">اسم المتجر</span>
            <Input
               type="text"
               placeholder="اسم المتجر"
               v-model="profileData.profileName"
               style="padding:10px"
            ></Input>

            <span style="padding:10px">اسم المتجر ( انكليزي )</span>
            <Input
               type="text"
               placeholder="اسم المتجر"
               v-model="profileData.profileNameEn"
               style="padding:10px"
            ></Input>

            <span style="padding:10px">نبذة عنا</span>
            <Input
               type="textarea"
               style="padding:10px"
               v-model="profileData.aboutUs"
               :autosize="{ minRows: 2, maxRows: 5 }"
               placeholder="نبذة عنا"
            ></Input>

            <span style="padding:10px">نبذة عنا ( انكليزي )</span>
            <Input
               type="textarea"
               style="padding:10px"
               v-model="profileData.aboutUsEn"
               :autosize="{ minRows: 2, maxRows: 5 }"
               placeholder="نبذة عنا"
            ></Input>
            <span style="padding:10px">رقم الهاتف</span>
            <Input
               type="text"
               v-model="profileData.phoneNumber"
               placeholder="رقم الهاتف"
               style="padding:10px"
            ></Input>
            <span style="padding:10px">رقم الواتس</span>
            <Input
               type="text"
               v-model="profileData.whats"
               placeholder="رقم الواتس"
               style="padding:10px"
            ></Input>
            <span style="padding:10px">الايميل</span>
            <Input
               type="text"
               v-model="profileData.email"
               placeholder="الايميل"
               style="padding:10px"
            ></Input>
            <span style="padding:10px">الفيسبوك</span>
            <Input
               type="text"
               v-model="profileData.facebook"
               placeholder="الفيسبوك"
               style="padding:10px"
            ></Input>
            <span style="padding:10px">السناب جات</span>
            <Input
               type="text"
               v-model="profileData.snapchat"
               placeholder="السناب جات"
               style="padding:10px"
            ></Input>
            <span style="padding:10px">سعر الدولار</span>
            <div style="padding:10px">
            <input type="number" class="type-number-input" v-model="profileData.dollarPrice"  placeholder="سعر الدولار"style="width:100%;direction:rtl"></input>
            </div>

            <Button type="success" @click="saveDate">حفظ التغييرات</Button>
         </Col>
         
      </Row>
   </div>
</template>

<script>
export default {
   data() {
      return {
          profileData: {},
          show : false
      };
   },
   created() {
      this.$http.get(`${this.$store.state.apiUrl}/profile/1`).then((res) => {
         console.log(res.data);
         this.profileData = res.data;
         this.show = true
      });
   },
   methods:{
       saveDate(){
          let self = this;

            this.$http.put(`${this.$store.state.apiUrl}/profile/1`, self.profileData).then((res) => {
               console.log(res)
                self.$Message.success({
                     title: "تم رفع البيانات",
                     content: "تم رفع البيانات",
                  });
            }).catch(e => {
                self.$Message.console.error({
                title: "حدث خطأ",
                content: "حدث خطأ في رفع البيانات الرجاء التأكد من المعلومات  ",
                });

               console.log(e)
            })
       }
   }
};
</script>

